.command-center-container {
  
  position: relative;
  width: 390px;
  height: 844px;
  flex-shrink: 0;
  border-radius: 48px;
    background-color: #EDEDED;
    scroll-behavior: smooth;
    overflow-y: scroll;
    overflow-x: hidden; /*stops horizontal scroll*/

}

.command-center {
  position: absolute;
  background-image: url("../../Assets/CommandCenter/CCbg.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; /* This will cover the entire area of the div, you can adjust as needed */
  width: 414px;
  height: 407px;
}

.roamstead-chat {
  position: absolute;
  background-image: url("../../Assets/RoamsteadChat/sunflowersBG.png");
