 h1 {
  position: absolute;
  top: 50%;
  display: flex;
  width: 348px;
  height: 33px;
  flex-direction: column;
  justify-content: center;
  flex-shrink: 0;
  margin: 20px;
  text-align: left;
  padding: 0% 0% 0% 0%;
  
  color: #FFF;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: Merriweather;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: 32.5px; /* 108.333% */
  letter-spacing: -0.48px;  
}

.cc-rounded-tile {
  margin: 80% 0% 0% 0%;
  background: url("../../Assets/CommandCenter/roundedTile.png") no-repeat;
  display: flex;
  width: 390px;
  height: 100%;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-shrink: 0;
  fill: linear-gradient(181deg, #FFF 49.92%, #EDEDED 80.94%);
}

.yellow-circle {
  display: flex;
  justify-content: center;
;
  
}

.yellow-circle img {
margin-top: -40%;
width: 103px;
height: 99px;
cursor: pointer;
}

.cc-rounded-tile img:hover {
  opacity: 0.8;
}

