.menu-header {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    background: url(../../Assets/MenuPage/MenuHeaderBG.png);
    background-size: cover;
    background-position: center;
    width: 394px;
    height: 186px;
    flex-shrink: 0;
}

.menu-title {
  position: absolute;
  margin-left: -70%;
  margin-top: 20%;
  width: 90px;
height: 36px;
flex-shrink: 0;
color: var(--base-black, #000);
text-align: center;
font-family: Quicksand;
font-size: 24px;
font-style: normal;
font-weight: 700;
line-height: normal;
}

.profile-picture {
  margin: 40% 0% 0% 0%;
}

.back-arrow:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.menu-section-container {
  margin-top: 15%;
  padding: 0 2%;  
  width: 370px;
  height: 100%; /* adjust this if the data starts going out of container */
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: space-between;
  /* grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto); */
}


.menu-item-title {
  display: flex;
width: 90%;
height: 17px;
flex-direction: row;
align-items: center;
justify-content: space-between;
padding: 0 0 0 10px;
flex-shrink: 0;
color: var(--Dark-Green, #435A10);
font-family: Quicksand;
font-size: 17px;
font-style: normal;
font-weight: 500;
line-height: 24px; /* 141.176% */
letter-spacing: 0.1px;

}

.menu-item-list p {
  display: flex;
  padding: 0 0 0 16px;
flex-direction: row;
align-items: center;
justify-content: space-between;
width: 197px;
height: 42px;
flex-shrink: 0;flex-shrink: 0;
border-radius: var(--main-system-20-px, 20px);
background: #FFF;
list-style: none;
margin: 0 0 8px 40px;


box-shadow: 5px 5px 8px 0px rgba(54, 70, 20, 0.10), -4px -4px 15px 0px rgba(46, 50, 40, 0.05);
}

.menu-item-list p:hover {
  cursor: pointer;
  color: #163500;
  background: #fff0b8;
}

.narrow-tile-menu-page {
  background-color: #FFEEB1;
}

.menu-arrow {
  display: flex;
  flex-direction: row;
  width: 10px;
  height: 20px;
  flex-shrink: 0;}

.menu-sub-item {
  display: flex;
width: 145px;
height: 26px;
flex-direction: column;
justify-content: flex-end;
flex-shrink: 0;
color: var(--1---Primary-Color, #658229);
font-family: Quicksand;
font-size: 17px;
font-style: normal;
font-weight: 500;
line-height: 24px; /* 141.176% */
letter-spacing: 0.1px;
}

.three-dot-container {
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  justify-content: flex-start;
  align-items: center;
  width: 28%;
}


.menu-item-three-dots {
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
}

.menu-page-two-container {
  margin: 50%;
}